<template>
  <div class="detail">
    <van-nav-bar title="书籍详情" left-arrow class="van-head" @click-left="back"></van-nav-bar>

    <div class="detail-book">
      <div class="clearfix">
        <div class="detail-book-img fl">
          <img class="auto-img" :src="detailBookData.cover" alt />
        </div>
        <div class="detail-book-item fr">
          <div class="detail-book-title">{{detailBookData.title}}</div>
          <div class="detail-book-bookmask clearfix">
            <div class="fl detail-book-moods">{{detailBookData.author}}</div>
            <div class="fl detail-book-center">|</div>
            <div class="fl">{{detailBookData.majorCate}}</div>
            <div class="fl detail-book-center">|</div>
            <div class="fl">{{detailBookData.chaptersCount}}万字</div>
          </div>
          <div class="detail-book-bookmask">{{detailBookData.updated}}更新</div>
        </div>
      </div>

      <div class="clearfix read-book">
        <div
          class="fl detail-book-join read-join"
          :class="{'addbook-dt':isAtBook}"
          @click="addBookshelf"
        >加入书架</div>
        <div
          class="fr detail-book-read read-join"
          :class="{'reading-dt':readSwitch}"
          @click="readingNow(catalogueLink,detailBookData._id)"
        >{{readSwitch?'继续阅读':'开始阅读'}}</div>
      </div>

      <div class="detail-free">开通VIP，免费阅读此书</div>
    </div>

    <div class="clearfix detail-item">
      <div class="fl detail-item-box">
        <div class="detail-item-title">总人气</div>
        <div class="detail-item-number">{{detailBookData.totalFollower}}万</div>
      </div>
      <div class="fl detail-item-box">
        <div class="detail-item-title">读者留存</div>
        <div class="detail-item-number">{{detailBookData.retentionRatio}}%</div>
      </div>
      <div class="fl detail-item-box">
        <div class="detail-item-title">总评分</div>
        <div class="detail-item-number">{{detailBookData.starRatingCount}}</div>
      </div>
    </div>

    <div :class="isActive ? 'detail-content-txt' : 'detail-content-title'">
      {{detailBookData.longIntro}}
      <div class="detail-content-icon" @click="swich">
        <van-icon :name="isActive ? 'arrow-down' : 'arrow-up'" size="18" />
      </div>
    </div>
    <div class="detail-catalog clearfix" @click="skipLink(detailBookData._id)">
      <div class="fl">目录</div>
      <div class="fr detail-catalog-title clearfix">
        <div class="fl">{{detailBookData.lastChapter}}</div>
        <div class="fl icons">
          <van-icon name="arrow" size="14" />
        </div>
      </div>
    </div>

    <div class="detail-comment">
      <div class="detail-comment-topic">热门书评</div>
      <div class="observer clearfix" v-for="(item,index) in bookReviewData" :key="index">
        <div class="fl detail-comment-img">
          <img class="auto-img border-img" src="../assets/image/chichigua.jpg" alt />
        </div>
        <div class="fr detail-critic">
          <div class="detail-comment-nickname">{{item.author.nickname}}</div>
          <div class="detail-comment-content">{{item.content}}</div>
          <div class="detail-comment-time">{{item.updated}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../assets/css/detail.less";
export default {
  data() {
    return {
      isActive: true,
      // 书籍详情
      detailBookData: {},
      // 书籍短评
      bookReviewData: [],
      // 书籍id
      booksId: "",
      //阅读开关
      readSwitch: false,
      //加入书架
      isAtBook: false,
      //默认章节id
      catalogueLink: "",
      //存储章节数据
      catalogueData: [],
      //储存历史阅读
      historyData: [],
      urls:''
    };
  },
  created() {
    this.isActive = true;
    this.getDetailBookData();
    this.getIsAtBook();
    this.getCatalogue();
    this.getBookReview();
    this.getReadSwitch();
  },

  methods: {
    //返回
    back() {
      this.$router.go(-1);
    },
    //路由跳转
    togglerTarBar(name) {
      this.$router.push(name);
    },
    swich() {
      this.isActive = !this.isActive;
    },
    //获取书籍详情
    getDetailBookData() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "GET",
        url: "http://novel.kele8.cn/book-info/" + this.$route.query.id,
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data) {
            this.detailBookData = result.data;
          }
          // 截取时间字符串
          this.detailBookData.updated = this.detailBookData.updated.substr(
            0,
            10
          );
          //图片
          this.detailBookData.cover = unescape(result.data.cover).slice(7);
          //留两位小数点
          this.detailBookData.totalFollower = (this.detailBookData.totalFollower / 10000).toFixed(2);
          this.detailBookData.starRatingCount =(this.detailBookData.totalFollower / 100).toFixed(2);
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //获取书籍短评
    getBookReview() {
      this.axios({
        method: "GET",
        url:
          "http://novel.kele8.cn/book/short-reviews?book=" +
          this.$route.query.id,
      })
        .then((result) => {
          if (result.data.docs) {
            //截取前三条评论
            this.bookReviewData = result.data.docs.slice(0, 3);
            //
          }
          //截取时间字符串
          for (let i = 0; i < this.bookReviewData.length; i++) {
            this.bookReviewData[i].updated = this.bookReviewData[
              i
            ].updated.substr(0, 10);
            //
          }
        })
        .catch((err) => {});
    },
    //跳转目录链接
    skipLink(id) {
      this.$router.push({
        name: "Catalogue",
        query: {
          id: id,
        },
      });
    },

    // 加入书架
    addBookshelf() {
      //   localStorage.getItem("BSID"):获取本地存储BSID
      if (localStorage.getItem("BSID")) {
        //   把序列化成字符串的数据反序列化为数组
        let ids = JSON.parse(localStorage.getItem("BSID"));
        //
        let isSave = ids.find((v) => {
          // 当前已有id的时候return true否则false
          if (this.detailBookData._id == v) {
            this.$toast("书架已存在此书籍");
            return true;
          } else {
            return false;
          }
        });
        if (!isSave) {
          this.isAtBook = true;
          this.$toast("已加入书架");
          // 将id加入数组前面
          ids.unshift(this.detailBookData._id);
        }
        // 把ids覆盖BSID(存储到本地需要把数据转化为字符串形式，否则会报错)
        localStorage.setItem("BSID", JSON.stringify(ids));
      } else {
        //   本地存储BSID不存在则创建此key
        let ids = [this.detailBookData._id];
        // 创建一个BSID本地长期存储，将字符串化的ids加入该key中
        localStorage.setItem("BSID", JSON.stringify(ids));
      }
      // localStorage.setItem("BSID")
    },

    // 查询是否在书架中
    getIsAtBook() {
      let id = this.$route.query.id;
      let ids = JSON.parse(localStorage.getItem("BSID"));
      if (!ids) {
        this.isAtBook = false;
        return;
      }
      let isSave = ids.find((v) => {
        // 当前已有id的时候return true否则false
        if (id == v) {
          return true;
        } else {
          return false;
        }
      });
      this.isAtBook = isSave;
    },

    //获取章节link
    getCatalogue() {
      this.axios({
        method: "GET",
        url:
          "http://novel.kele8.cn/book-sources?view=summary&book=" +
          this.$route.query.id,
      })
        .then((result) => {
          if (result.status == 200) {
            this.urls = result.data[0]._id;
            //
          }

          this.axios({
            method: "GET",
            url: "http://novel.kele8.cn/book-chapters/" + this.urls,
          })
            .then((result) => {
              if (result.status == 200) {
                this.CatalogueData = result.data.chapters;
                this.catalogueLink = this.CatalogueData[0].link;
                this.$store.dispatch("setReadNow", result.data.chapters);
              }
            })
            .catch((err) => {});
        })
        .catch((err) => {});
    },
    //开始阅读
    readingNow(url, id) {
      let isStat = true;
      if (this.historyData.length != 0) {
        this.historyData.map((v) => {
          if (v.bookId == id) {
            isStat = false;
            this.$router.push({
              name: "Readnow",
              query: {
                url: v.chapterId,
                id: id,
                index: v.index,
              },
            });
            return;
          }
        });
      }
      if (isStat) {
        this.$router.push({
          name: "Readnow",
          query: {
            url: url,
            id: id,
            index: 0,
          },
        });
      }

    },
    //获取开关
    getReadSwitch() {
      this.historyData = this.$store.state.readHistory;
      let id = this.$route.query.id;
      this.historyData.map((v) => {
        if (v.bookId == id) {
          this.readSwitch = v.switch;
          return;
        }
      });
    },
  },
};
</script>